import React, { useState } from 'react';
import { Typography, TextField, Button,  Box} from '@mui/material';
import firstImage from '../data/first.jpg'; //  

const Header = ({ onStart }) => {
  const [myname, setName] = useState('');

  const handleChange = (event) => {
    setName(event.target.value);
  };

  const handleKeyDown = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault(); // Enter    
      onStart(); //    
    }
  };

  const handleSubmit = () => {
    if (myname) {
      onStart(myname); //    
    }
  };


  return (
    <Box 
      style={{ textAlign: 'center', margin: '20px 0' }} //   
    >
      <Typography variant="h4" gutterBottom>
         !!
        
      </Typography>
      <Typography variant="body1" gutterBottom>
              
      </Typography>
      {/*   */} 
      <img 
        src={firstImage} //  import 
        alt=""
        style={{ width: '80%', maxWidth: '400px', margin: '20px auto' }} //  
      />
      <Box style={{ marginBottom: '20px', maxWidth: '400px', margin: '0 auto' }}>
        <TextField
          label=" "
          value={myname}
          onChange={handleChange}
          onKeyDown={handleKeyDown}
          variant="outlined"
          InputProps={{
            style: { borderColor: '#FFB6CD' }, //  
          }}
          sx={{
            margin: '0 20px',
            marginBottom: '20px',
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#FFB6CD', //   
              },
              '&:hover fieldset': {
                borderColor: '#FFB6CD', //     
              },
              '&.Mui-focused fieldset': {
                borderColor: '#FFB6CD', //    
              },
            },
          }}
        />
      </Box>
      <Button 
        variant="contained" 
        color="primary" 
        onClick={handleSubmit}
        style={{ marginTop: '10px', backgroundColor: '#FFB6CD',fontWeight: 'bold' }} //   
      >
        
      </Button>
    </Box>
  );
};


export default Header;
